    //     max-height: 22px;
    // }
    // img[src*="https://cloud.tagbox.com/react-app/media/images/users/05.svg"] {
    //     max-height: 26px;
    // }
}

.bg-gradient-1 {
    // background: radial-gradient(30.69% 108.71% at 29.63% 82.87%, rgba(239, 221, 184, 0.4) 0%, rgba(252, 254, 254, 0.4) 100%),
    //     radial-gradient(35.13% 35.18% at 73.15% 17.18%, rgba(163, 119, 235, 0.4) 0%, rgba(238, 252, 252, 0.4) 100%);
    background: radial-gradient(30.69% 108.71% at 29.63% 82.87%, rgba(239, 221, 184, 0.4) 0%, rgba(252, 254, 254, 0.4) 100%),
        radial-gradient(35.13% 35.18% at 73.15% 17.18%, rgba(65, 121, 255, 0.2) 0%, rgba(238, 252, 252, 0.4) 100%);

}

.bg-gradient-2 {
    background: radial-gradient(30.69% 108.71% at 29.63% 82.87%, rgba(239, 221, 184, 0.4) 0%, rgba(252, 254, 254, 0.4) 100%),
        radial-gradient(35.13% 35.18% at 73.15% 17.18%, rgba(255, 64, 81, 0.2) 0%, rgba(238, 252, 252, 0.4) 100%);
}

.register_check {
    padding-left: 0;
    list-style: none;
}

.auth_form_area {
    max-width: 500px;
    margin: 0 auto;
    width: 100%;

    a.link-active {
        color: #212529;
        font-weight: 600;

        &:hover {
            color: #212529 !important;
            text-decoration: underline;
        }
    }
}

.suite_logo svg path {
    fill: var(--bs-light);
}

.white_transparent,
.white_transparent .h-40px {
    background-color: rgba(var(--bs-primary-rgb), .25);
}

.auth_form {
    min-height: calc(100vh - 110px);
}

/**/
.other-info-main {
    background-color: var(--bs-app-body-bg) !important;

    .auth_form_area {
        max-width: 700px;
    }
}

/* Start Tagembed Redirect Pop */
.popup-wrap .popup-inn {
    border-radius: 10px;
    // max-width: 555px;
    background: #fff;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    position: relative;
    z-index: 999;
}

.popup-wrap .popup-inn p {
    font-size: 16px;
    color: #2B2B2B;
    margin: 30px 20px;
}

.popup-wrap .popup-head {
    background: #EDEDED;
    border-radius: 10px 10px 0 0;
    padding: 10px 15px 6px;
}

.popup-wrap .popup-head img {
    max-width: 100%;
    height: 50px;
}

.popup-wrap span.popup-try {
    cursor: pointer;
    color: var(--bs-primary);
}

.popup-wrap span.popup-try:hover {
    opacity: 0.85;
}

.review-bnn+div .subheader {
    top: 85px;
}

.hauto-smheight {
    table {
        tbody {
            tr {
                &:first-child {
                    td {
                        &:first-child {
                            img {
                                width: 90px;
                            }
                        }
                    }
                }
            }
        }
    }
}

/* Stop Tagembed Redirect Pop */

@media (min-width: 992px) {
    .auth_form {
        min-height: 100vh;
    }

    .auth_full_screen .auth_form {
        min-height: calc(100vh - 66px);
    }
}

@media (max-height: 550px) {
    .hauto-smheight {
        height: auto !important;
    }
}